.cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: center;
  gap: 1.875rem;
}
@media (min-width: ) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.25em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  flex: 1;
  background-color: var(--cyan);
  color: var(--white);
  padding: 1.5625rem;
  text-align: center;
}
.card__name {
  color: var(--white);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__descript {
  font-size: 1.125rem;
  color: --softblue;
}
.card__price {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  min-width: none;
  background: var(--verydarkblue-main-bg);
  font-family: var(--font-outfit);
}

:root {
  /* Primary Colors : */
  --softblue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  /* Neutral Colors : */
  --verydarkblue-main-bg: hsl(217, 54%, 16%);
  --verydakrblue-card-bg: hsl(216, 50%, 16%);
  --verydarkblue-line: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.25em) {
  .container {
    padding: 0;
  }
}